import { createAction } from 'redux-actions';
import { Goods } from '../goods/model';
import { SelectGoods } from './models'; 
import * as AT from '../../constants/ActionTypes';

export const selectGoods = createAction < SelectGoods, Goods >(
  AT.SELECT_GOODS,
  (goods: Goods) => ({ goods})
);



