import { handleActions, Action } from 'redux-actions';
import { BasketState, SelectGoods, GoodsCount,  } from './models';
import * as AT from '../../constants/ActionTypes';
import { Goods } from '../goods/model';

const initialState: BasketState = <BasketState> {
    SelectedGoods: [],
    counterList: [],
    TotalPayment: 0
};

const countGoods = (selectedGoods: Goods[]): GoodsCount[] => {
        const result: GoodsCount[] = [];
        for (let goods of selectedGoods) {
            const counter = result.filter((r) => (r.id === goods.id))[0];
            if (counter) {
                counter.count++;
            } else {
                result.push({
                    count: 1,
                    img: goods.img,
                    displayName: goods.displayName,
                    id: goods.id,
                    price: goods.price
                });
            }
        }
        return result;
 };

 const calcSum = (counterList: GoodsCount[]): number => {
    let sum = 0;
    for (let counter of counterList) {
        sum += (counter.price * counter.count);
    }
    return sum;
 };

export default handleActions<BasketState, SelectGoods>(
    {
        [AT.SELECT_GOODS]: (state: BasketState, action: Action<SelectGoods>): BasketState => {
            if (!action.payload) {
                return state;
            }
            const newState = Object.assign({}, state);
            const newSelectedList = Array.apply(null, newState.SelectedGoods);
            newSelectedList.push(action.payload.goods);
            newState.SelectedGoods = newSelectedList;
            newState.counterList = countGoods(newSelectedList);
            newState.TotalPayment = calcSum(newState.counterList);
            return newState;
        }
    }, 
    initialState
);


